.post {
    background: white;
    width: 600px;
    /* max-width: 500px; */
    border: 1px solid lightgray;
    margin-bottom: 50px;
}

.post__image {
    width: 100%;
    object-fit: contain;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
}

.post__text {
    font-weight: normal;
    padding: 10px;
}

.post__header {
    display: flex;
    align-items: center;
    padding: 10px
}

.post__avatar {
    margin-right: 1%;
}

.post__comments {
    padding: 10px;
}

form.comment__form {
    display: flex;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    padding: 15px;
}

.comment__button {
    padding-left: 5%;
}

@media screen and (max-width: 767px) {

    .post {
        width: 300px;
      }
    
      .post__header > h3 {
        font-size: 15px;
      }
    
      .post__text {
        font-size: 15px;
      }
    
      .post__comments {
        font-size: 15px;
      }
      
      .css-zgt3rr-MuiAvatar-root {
        width: 30px !important;
        height: 30px !important;
      }
};