* {
  margin: 0;
}

.app {
  background: #F9F9F9;
}

.nav{
  display: flex;
  width: 100%;
  background: white;
  border-bottom: 1px solid lightgray;
  padding: 20px 0;
  z-index: 1030;
  position: fixed;
  top: 0;
}

.user__buttons {
  display: flex;
  width: 50%;
  justify-content: flex-end;
}

.app__posts {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  margin-top: 6%;
}

/* MEDIA QUERIES */
@media screen and (max-width: 767px) {
  .app__posts {
    margin-top: 20%;
  }
}

@media screen and (max-width: 1023px) {
  .app__posts {
    margin-top: 9.1%;
  }
};
